import React from 'react';
import { useNavigate } from "react-router-dom";
import './modal.css';
import LongButton from './LongButton';
import fire from '../img/icon/purpleFire.svg';
import lightFire from '../img/icon/fire.svg';

/**
 * Modal 
 *
 * - onClose:     
 * - isLoggedIn:  
 */
const Modal = ({ onClose, isLoggedIn }) => {
    const navigate = useNavigate();

    //   
    const goLogin = () => {
        onClose();
        navigate("/login");
    }

    //   
    const goHome = () => {
        onClose();
        navigate("/");
    }

    return (
        <div className='modal'>
            {/*  */}
            <div className='modal-bg'></div>
            <div className='modal-body'>
                {/*   */}
                <div className='modal-header'>
                    {isLoggedIn ? (
                        <div> !</div>
                    ) : (
                        <div>
                             <br />     !
                        </div>
                    )}
                </div>
                {/*   */}
                <div className='modal-content'>
                    {isLoggedIn ? (
                        //      
                        <div
                            className='modal-studied-number'
                            style={{
                                display: "flex",
                                justifyContent: "center",
                                width: "100%",
                            }}
                        >
                            <object data={lightFire}></object>
                            <span
                                style={{
                                    fontWeight: 700,
                                    fontSize: "24px",
                                    color: "#B076F9"
                                }}
                            >
                                +1
                            </span>
                        </div>
                    ) : (
                        //      
                        <div className='modal-studied-wrapper'>
                             
                            <div className='modal-studied-number'>
                                <object data={fire}></object>
                                100
                            </div>
                        </div>
                    )}
                    {/*   */}
                    <div className='example-box'></div>
                </div>
                {/*    */}
                <div className='modal-btns'>
                    {/*   */}
                    {!isLoggedIn && (
                        <div
                            className='modal-btn'
                            style={{ marginTop: "80px" }}
                        >
                            <LongButton
                                color="purple"
                                content=""
                                onClick={goLogin}
                                width="250px"
                            />
                        </div>
                    )}
                    {/*   */}
                    <div className='modal-btn'>
                        <LongButton
                            color="purpleLine"
                            content=" "
                            onClick={goHome}
                            width="250px"
                        />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Modal;
